var LayOut = React.createClass({
	render: function(){
		return (
			<div>
				<div id="header"></div>
				<div id="banner"></div>
				<div id="main"></div>
				<div id="list"></div>
				<div id="footer"></div>
			</div>
		)
	}	
});
module.exports = LayOut;


