var List = React.createClass({
	getDefaultProps:function(){
		return {
			"url":"http://localhost:9000/list"
		}
	},
	getInitialState:function(){
		return {
			"result":""
		}
	},
	componentWillMount:function(){
		var that = this;
		$.ajax({
			type:"get",
			url:this.props.url,
			success:function(res){
				that.setState({
					"result":res
				})
			}
		});
	},
	render: function(){
		var arr = [];
		var result = this.state.result.jspt;
		if(result){
			for(var i in result){
				arr.push(<div>
					<img src={result[i].img01} style={styles.bannerimg}/>
					<img src={result[i].img02} style={styles.bannerimg}/>
				</div>)
			}
		}
		return (
			<div >
				{arr}
			</div>
		)
	}
});
module.exports = List;


